  }
  to {
    transform: translateX(60%);
  }
}

.bg-shapes div {
  position: absolute;
  opacity: 0.2;
}
.bg-shapes div:nth-child(1) {
  height: 200px;
  width: 200px;
  top: 40%;
  left: calc(0% - 100px);
  background-color: #ffffff;
  border-radius: 50%;
  animation: shape01 10s linear infinite;
}

.bg-shapes div:nth-child(2) {
  height: 80px;
  width: 80px;
  top: 20%;
  right: -10%;
  background-color: transparent;
  border: 5px solid #ffffff;
  animation: shape02 10s linear infinite;
  
}

.bg-shapes div:nth-child(3) {
  height: 60px;
  width: 60px;
  top: 80%;
  right: -10%;
  background-color: transparent;
  border: 5px solid #ffffff;
  border-bottom: 5px solid transparent;
  border-left: 5px solid transparent;
  border-radius: 50%;
  animation: shape03 7s linear infinite;
}
@keyframes shape01 {
  0%,
  100% {
    transform: scale(0.5);
  }
  50% {
    transform: scale(1);
  }
}


@keyframes shape02 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes shape03 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
