.projectBody {
  margin-top: 3%;
}

.projectHead {
  margin-bottom: 4%;
  text-align: center;
}

.projectHead span {
  font-size: 27px;
  border-bottom: 7px;
  font-family: 'Fredoka One';
  color: #11998e;
  letter-spacing: 4px;
  text-transform: uppercase;
}

.projectcard {
  width: 12px;
  margin: auto;
}

.projectItem {
  margin-top: 5px;
  padding: 10px 20px;
  text-align: center;
  opacity: 1;
  transform: translate(0px, 0px);
}

.projectGrid {
  width: 90vw;
  margin: 0 auto;
  max-width: 1100px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  column-gap: 8px;
  row-gap: 1rem;
  margin-bottom: 16px;
}
.projectContainer {
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 32px 0 grey;
}
.link1,
.link2 {
  position: absolute;
  right: 0;
  background-color: rgb(55, 150, 131, 0.9);
  border: none;
  padding: 0.5rem 0.75rem;
  font-weight: bold;
  transition: all 0.3s linear;
  transform: translateX(101%);
  cursor: pointer;
  color: #fff;
}
.link1 a,
.link2 a {
  color: #fff;
}

.link1 {
  top: 40%;
}
.link2 {
  top: 70%;
}

.fa-github-square {
  margin-right: 0.5rem;
}

.projectcard {
  width: 12px;
  margin: auto;
}

.projectContainer:hover .link1,
.projectContainer:hover .link2 {
  transform: translateX(0);
}

.projectImage {
  display: block;
  width: 100%;
  min-height: 15rem;
  transition: all 0.3s linear;
  background-position: center;
  background-size: cover;
}

.projectContainer:hover .projectImage {
  opacity: 0.5;
}

.divisor {
  width: 100%;
  height: 2px;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2), 0 -3px 8px rgba(255, 255, 255, 1);
}

.projectText {
  color: #11998e;
  margin-bottom: 0;
  font-weight: 900;
}

.projectTool {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.projectTool p {
  font-size: 13px;
  font-weight: 700;
  margin-right: 8px;
  border-radius: 3px;
  color: #11998e;
  background-color: rgb(185, 204, 183);
  padding: 5px 10px;
  font-family: "Roboto Mono", monospace;
  margin-bottom: 0;
  margin-top: 10px;
}

.projectTool p:nth-child(1) {
  font-weight: 900;
}

@media only screen and (min-width: 1071px) {
  .projectBody {
    margin-top: 3%;
  }
  .projectHead {
    margin-bottom: 2%;
    text-align: center;
  }
  .projectItem {
    margin-top: 15px;
    padding: 20px;
    text-align: center;
  }
  .projectGrid {
    width: 90vw;
    margin: 0 auto;
    max-width: 1100px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    column-gap: 1.5rem;
    row-gap: 2rem;
    margin-bottom: 16px;
  }
}
